<template>
  <PageWrapper dense contentFullHeight fixedHeight contentClass="flex" style="position: relative">
    <CommonTree
      title="电站组织"
      @select="handleSelect"
      :value="treeData"
      class="left-tree-layout"
      :toolbar="true"
      :canEdit="false"
      :canAdd="false"
      :canDelete="false"
      :isShowOperationBtns="false"
      :fieldNames="{ key: 'id', title: 'name' }"
    />
    <div class="right-table-layout">
      <BasicTable
        @register="registerTable"
        class="fix-basic-table"
        :searchInfo="searchInfo"
        @resize-column="(w, col) => (col.width = w)"
        @row-db-click="(record) => handleView(record)"
        @selection-change="handleSelectionChange"
      >
        <template #toolbar>
          <a-button type="primary" :preIcon="IconEnum.ADD" @click="handleCreate">{{
            t('common.action.create')
          }}</a-button>
          <a-button
            :disabled="disabledBtn"
            class="red-btn"
            :preIcon="IconEnum.DELETE"
            @click="handleDelete"
            >{{ t('common.delText') }}</a-button
          >
          <a-button class="yellow-btn" :preIcon="IconEnum.EXPORT" @click="handleExport">{{
            t('common.action.export')
          }}</a-button>
        </template>
      </BasicTable>
    </div>
  </PageWrapper>
</template>
<script lang="ts" setup>
  import { onMounted, reactive, ref } from 'vue';
  import { useMessage } from '@/hooks/web/useMessage';
  import { useI18n } from '@/hooks/web/useI18n';
  import { IconEnum } from '@/enums/appEnum';
  import { BasicTable, useTable, SorterResult } from '/@/components/Table';
  import { TreeItem } from '/@/components/Tree';
  import { PageWrapper } from '/@/components/Page';
  import CommonTree from '@/components/Framework/Tree/CommonTree.vue';
  import { exportExcelFile } from '@/utils/file/download';
  import * as CommonUtil from '/@/utils/common';
  import * as ProcessInstanceApi from '@/api/bpm/processInstance';
  import {
    getTreeList,
    getPage,
    getData,
    exportExcel,
    columns,
    searchForm,
  } from './secondWorkTicket.data';
  import { assign } from 'lodash-es';
  import { addTabPage } from '/@/utils/route';
  import { MsgManager } from '/@/message/MsgManager';

  const { t } = useI18n();
  const message = useMessage();
  const treeData = ref<TreeItem[]>([]);
  const searchInfo = reactive<any>({});
  const checkedNode = reactive<any>({});
  const disabledBtn = ref<boolean>(true);

  const getParam = (isPage: boolean) => {
    const paginationRef = getPaginationRef() as any;
    const query = {} as any;
    assign(query, searchInfo);
    if (!query.sortOrder) {
      query.sortField = 'fillinDate';
      query.sortOrder = 'desc';
    }
    if (isPage) {
      query.pageNo = paginationRef.current;
      query.pageSize = paginationRef.pageSize;
    }
    return query;
  };

  const getPageByParam = async () => {
    const searchPageInfo = getParam(true);
    const pageList = await getPage(searchPageInfo);
    return pageList.result;
  };

  const tableTitle = ref<string>('电气第二种工作票');
  const [registerTable, { reload, getPaginationRef, getSelectRows, clearSelectedRowKeys }] =
    useTable({
      title: tableTitle,
      api: getPageByParam,
      columns: columns(),
      useSearchForm: true,
      formConfig: searchForm,
      bordered: true,
      rowSelection: {
        type: 'radio',
      },
      sortFn: (sortInfo: SorterResult) => {
        const sortOrder = sortInfo.order?.replace('end', '');
        searchInfo.sortOrder = sortOrder;
        searchInfo.sortField = sortOrder ? sortInfo.field : undefined;
      },
      handleSearchInfoFn(info) {
        assign(searchInfo, info);
        if (searchInfo.opendTimeStart) {
          searchInfo.opendTimeStart = searchInfo.opendTimeStart + ' 00:00';
        }
        if (searchInfo.opendTimeEnd) {
          searchInfo.opendTimeEnd = searchInfo.opendTimeEnd + ' 23:59';
        }
        return info;
      },
      showTableSetting: true,
      tableSetting: { fullScreen: true },
      showIndexColumn: true,
    });

  function handleCreate() {
    if (!checkedNode.stationId) {
      message.error('请选择电站组织');
      return;
    }
    addTabPage(
      `/po/ticket/secondworkticket/create?stationId=${checkedNode.stationId}&stationName=${checkedNode.stationName}&stationOrganId=${checkedNode.stationOrganId}&stationOrganName=${checkedNode.stationOrganName}`,
      getTabPageName(),
    );
  }

  function checkSelectData() {
    const rows = getSelectRows();
    if (rows.length == 0) {
      message.error('请选择一行数据');
      return false;
    }
    return rows[0];
  }

  const handleDelete = async () => {
    const record = checkSelectData();
    if (!record) {
      return;
    }
    await message.delConfirm();
    if (record.status != 0) {
      message.error(t('common.message.delNotDraftMessage'));
      return;
    }
    const res = await getData({ id: record.id });
    await ProcessInstanceApi.deleteProcessInstance(res.result?.processVariable?.processInstanceId);
    message.success(t('common.delSuccess'));
    clearSelectedRowKeys();
    reload();
  };

  function handleView(record: any) {
    CommonUtil.toFlowPage(record.id, tableTitle.value);
  }

  const getTabPageName = (record?: any) => {
    const name = tableTitle.value;
    if (!record?.id) {
      return `${t('common.action.create')}${name}`;
    }
    if (record?.status === 0) {
      return `${t('common.action.edit')}${name}`;
    }
    if (record?.status === 1 || record?.status === 6) {
      return `审批${name}`;
    }
    return `查看${name}`;
  };

  // 查询左侧树数据
  async function queryTreeList() {
    const typeList = await getTreeList({});
    treeData.value = typeList.result as unknown[] as TreeItem[];
  }

  // 左侧树选中事件
  function handleSelect(node) {
    searchInfo.stationOrganId = null;
    searchInfo.stationId = null;
    checkedNode.stationId = '';
    checkedNode.stationName = '';
    checkedNode.stationOrganId = '';
    checkedNode.stationOrganName = '';

    if (node) {
      if (node.nodeKindId === 'org') {
        searchInfo.stationOrganId = node.id;
      }
      if (node.nodeKindId === 'station') {
        searchInfo.stationId = node.powerStationId;
        checkedNode.stationId = node.powerStationId;
        checkedNode.stationName = node.powerStationName;
        checkedNode.stationOrganId = node.organId;
        checkedNode.stationOrganName = node.organName;
      }
    }
    reload();
  }

  function handleExport() {
    message.createConfirm({
      title: t('common.message.confirmTitle'),
      iconType: 'warning',
      content: t('common.message.exportMessage'),
      async onOk() {
        const query = getParam(false);
        query.filename = `${tableTitle.value}.xls`;
        query.excelList = columns();
        await exportExcel(query).then((res) => {
          exportExcelFile(res?.data, query.filename);
        });
        message.success(t('common.exportSuccessText'));
      },
    });
  }

  function handleSelectionChange({ rows }) {
    disabledBtn.value = rows.length === 0;
  }

  // 启动执行代码
  onMounted(() => {
    queryTreeList();

    MsgManager.getInstance().listen('second-work-ticket', reload);
  });
</script>
<style lang="less" scoped>
  @import '/@/design/biz/bizList.less';
</style>
